import React from 'react';

import axios from 'axios';

import Aux from '../../hoc/Aux';
import SectionHeader from '../Partials/SectionHeader';
import Table from '../Partials/Table';
import Modal from '../Partials/Modal/Modal';

export default class RequestedLoans extends React.Component {
  state = { show: false, modalContent: null, values: null };

  headers = [
    { name: 'Member ID', value: 'memberID' },
    { name: 'Surname', value: 'surname' },
    { name: 'E-mail', value: 'email' },
    { name: 'Amount', value: 'amount' },
    { name: 'Loan Type', value: 'loanType' }
  ];

  componentDidMount() {
    axios
      .get('/loans/requested')
      .then(response => this.setState({ values: response.data.data }))
      .catch(error => console.error(error));
  }

  declineLoan = (content) => {
    axios
      .get(`/loans/decline/${content.loanId}`)
      .then(response => this.updateState(content, false));
  }

  approveLoan = (content) => {
    axios
      .get(`/loans/approve/${content.loanId}`)
      .then(response => this.updateState(content, true));
  }

  updateState = (body, approval) => {
    const newModalContent = {...body, approval: approval};
    const oldState = this.state.values;
    oldState.map(obj => {
      if (obj.loanId === body.loanId) {
        obj.approval = approval;
        return obj;
      } else {
        return obj;
      }
    });
    this.setState({modalContent: newModalContent, values: oldState});
  }

  showModal = body => {
    const res = this.state.values.filter(obj => body.memberID === obj.memberID);
    this.setState({ show: true, modalContent: res[0] });
  };

  hideModal = () => {
    this.setState({ show: false });
  };
  render() {
    return (
      <Aux>
        <SectionHeader
          title="List Requested Loans"
          first="Loans"
          last="List Requested Loans"
        />
        <Table
          title="List of Requested Loans"
          headers={this.headers}
          content={this.state.values}
          openModal={this.showModal}
        />
        <Modal
          content={this.state.modalContent}
          show={this.state.show}
          handleClose={this.hideModal}
          footer={true}
          approveLoan={this.approveLoan}
          declineLoan={this.declineLoan}
        />
      </Aux>
    );
  }
}
